const NODE_ENV = process.env.NODE_ENV;
const ORIGIN = window.location.href.replace(window.location.pathname, '');

const REACT_APP_API_URL = (NODE_ENV === "production") ? ORIGIN + "/nservermessage/web/" : process.env.REACT_APP_API_URL;
const REACT_APP_AUTH_URL = (NODE_ENV === "production") ? ORIGIN + "/content/get_user_data" : process.env.REACT_APP_AUTH_URL;
const REACT_APP_SOCKET_URL = (NODE_ENV === "production") ? ORIGIN : process.env.REACT_APP_SOCKET_URL;
const REACT_APP_SOCKET_PATH = "/nservermessage/socket/";

const config = { REACT_APP_API_URL, REACT_APP_AUTH_URL, REACT_APP_SOCKET_URL, REACT_APP_SOCKET_PATH }

export default config;