import React, { Component } from 'react';
import searchicon from './assets/image/searchicon.svg';
import { connect } from 'react-redux';
import { mapState, mapDispatch } from './actions';
import { format } from 'date-fns';
import { socket, addUser } from './socketscript';
import defaultImage from './assets/image/user1.jpg';

function onImgError(e) {
  e.target.src = defaultImage;
}

class Left extends Component {
  state = { search: '', users: [] };
  peopleRef = React.createRef();
  submitTimeout = null;

  constructor(props) {
    super(props);
    addUser(this.props.currentUser.userId);
    socket.on('newsenders', senders => {
      if(senders.length > 0) {
        this.props.updateUsersList(this.props.users, senders, this.props.selectedUser.userId);
        this.handleClick((this.props.selectedUser.userId ? this.props.selectedUser : this.props.users[0]));
        // this.setState({ users: [...this.props.users] });
      }
    });
    socket.on('usersockets', ids => {
      if(ids.length > 0) {
        this.props.setSocketIds(ids);
      }
    })
    this.props.clearMessageCount({ userId: this.props.currentUser.userId }, this.props.token);
  }

  /* handleSubmit = (e) => {
    if (e) e.preventDefault();
    const search = this.state.search;
    if(search.trim()) {
      const filteredUsers = this.props.users.filter(n => (n.name.toLowerCase().indexOf(search) > -1));
      this.setState({ users: [...filteredUsers] })
    } else {
      this.setState({ users: [...this.props.users] })
    }
  }; */

  handleSubmit = (e) => {
    if (e) e.preventDefault();
    this.fetchUsers()
  }

  handleChange = (e) => {
    const val = e.target.value;
    this.setState({ search: (val ? val.trim() : '') });
    if(this.submitTimeout) {
      clearTimeout(this.submitTimeout);
    }
    this.submitTimeout = setTimeout(() => { this.handleSubmit() }, 500);
  };

  handleClick(user) {
    this.props.getMessages({ receiverId: this.props.currentUser.userId, senderId: user.userId }, this.props.token);
    this.props.updateUsers(this.props.users, user.userId);
    user.count = 0;
    this.props.setSelectedUser(user);
    this.props.updateUserState({ userId: user.userId, receiverId: user.receiverId }, this.props.token);
  };

  async fetchUsers() {
    try {
      let params = { receiverId: this.props.currentUser.userId, search: '' };
      params.search = this.state.search;

      const result = await this.props.getUsers(params, this.props.token)
      if(result) {
        this.handleClick(this.props.users[0]);
        // this.setState({ users: [...this.props.users] });
      } else {
        // this.setState({ users: [] });
      }
    } catch(e) {}
  }

  componentDidMount() {
    this.fetchUsers();
    window['$'](".people-list").slimScroll({ size: '10px', height:'100%' });
  }

  loadMoreUsers = async () => {
    let params = { receiverId: this.props.currentUser.userId, search: '' }
    params.skip = this.props.users.length;
    params.search = this.state.search;

    await this.props.getScrollUsers(params, this.props.users, this.props.token);
    this.props.updateUsers(this.props.users, this.props.selectedUser.userId);
    // this.setState({ users: [...this.props.users] });
  }

  handleScroll = (e) => {
    if ((this.peopleRef.current.scrollTop + this.peopleRef.current.clientHeight) >= this.peopleRef.current.scrollHeight && this.props.userScroll && !this.props.loadingUserList){
      this.loadMoreUsers();
    }
  }

  userLoading() {
    if(this.props.loadingUserList || (!this.props.loadingUserList && this.props.users.length === 0)) {
      return (
        <li className="person" data-chat="person1">
          <span className="people-img"></span>
          <div className="people-details">
            {this.props.loadingUserList && <span>Loading ...</span>}
            {(!this.props.loadingUserList && this.props.users.length === 0) && <span>Not Found!</span>}
          </div>
        </li>
      );
    }
    return;
  }

  render() {

    let userList = this.props.users.map(user => {
      const slashSplit = (user.image).split('/');
      const imageSplit = (slashSplit[slashSplit.length - 1]).split('.');
      const userImage = (imageSplit.length > 1) ? user.image : '';
      return (
        <li key={user.userId} className={user.classes} onClick={() => this.handleClick(user)} data-chat="person1">
          <span className="people-img"><img onError={onImgError} src={userImage ? (userImage + '?_' + (Math.floor(Math.random() * 1000) + 1)) : defaultImage} alt=""></img></span>
          <div className="people-details">
            {(user.count > 0) && <span className="green-dot-new">{user.count}</span>}
            <span className="name">{user.name}</span>
            <span className="time">{format(new Date(user.datetime), 'MMM Do YY, hh:mm a')}</span>
            <span className="preview">{user.message}</span>
          </div>
        </li>
      )
    });
  
    return (
      <div className="left">
        <div className="top">
          <div className="back-btn"></div>
          <form onSubmit={this.handleSubmit}>
            <input type="text" name="search" autoComplete="off" placeholder="Search by Name" onChange={this.handleChange}></input>
            <button className="search"><img src={searchicon} alt="Search Icon"></img></button>
          </form>
        </div>
        <div className="people">
          <ul className="people-list" ref={this.peopleRef} onScroll={this.handleScroll}>
            {userList}
            {this.userLoading()}
          </ul>
        </div>
      </div>
    );
  }
}

export default connect(mapState, mapDispatch)(Left);
