import config from './config.js';
const { REACT_APP_AUTH_URL, REACT_APP_API_URL } = config;
const usersPerPage = 100;
const messagesPerPage = 20;

export function mapState(state) {
    return { ...state };
}

function compare(a, b) {
    let a_datetime = new Date(a.datetime).getTime();
    let b_datetime = new Date(b.datetime).getTime();
    if (a_datetime < b_datetime)
        return 1;
    if (a_datetime > b_datetime)
        return -1;
    return 0;
}

export function mapDispatch(dispatch) {
    return {
        dispatch,
        // Fetch token from PHP server
        authenticate: async () => {
            try {
                const res = await fetch(REACT_APP_AUTH_URL, { credentials: "include" });
                const result = await res.json();
                
                result.error = (typeof result.error === 'string') ? (result.error === "true") : result.error;
                // console.log(result);
                if(!result.error) {
                    const user = {
                        userId: parseInt(result.data.student.id),
                        name: result.data.student.student_name,
                        image: result.data.student.student_image
                    }
                    dispatch({ type: 'SET_STATE', value: { token: result.data.token, currentUser: user, isAuthenticated: true } });
                    return true;
                }
                return false;
            } catch(e) {
                return false;
            }
        },
        // Fetch userslist from node server
        getUsers: async (data, token) => {
            try {
                dispatch({ type: 'SET_STATE', value: { loadingUserList: true, users: [] } });
                
                const res = await fetch(`${REACT_APP_API_URL}users`, {
                    method: "POST",
                    mode: "cors",
                    cache: "no-cache",
                    headers: {
                        "Content-Type": "application/json; charset=utf-8",
                        Authorization: `Bearer ${token}`
                    },
                    redirect: "follow",
                    referrer: "no-referrer",
                    body: JSON.stringify(data),
                });
        
                const result = await res.json();
                // console.log(result);
                if(result.success && result.data.length > 0) {
                    dispatch({ type: 'SET_STATE', value: { users: result.data, loadingUserList: false, userScroll: ((result.data.length % usersPerPage) === 0) } });
                    return true;
                }

                dispatch({ type: 'SET_STATE', value: { users: [], messages: [], messageScroll: false, selectedUser: { name: "" }, loadingUserList: false } });
                return false;
            } catch(e) {
                dispatch({ type: 'SET_STATE', value: { users: [], messages: [], messageScroll: false, selectedUser: { name: "" }, loadingUserList: false } });
                return false;
            }
        },
        // Fetch userslist from node server
        getScrollUsers: async (data, users, token) => {
            try {
                dispatch({ type: 'SET_STATE', value: { loadingUserList: true } });

                const res = await fetch(`${REACT_APP_API_URL}users`, {
                    method: "POST",
                    mode: "cors",
                    cache: "no-cache",
                    headers: {
                        "Content-Type": "application/json; charset=utf-8",
                        Authorization: `Bearer ${token}`
                    },
                    redirect: "follow",
                    referrer: "no-referrer",
                    body: JSON.stringify(data),
                });
        
                const result = await res.json();
                // console.log(result);
                if(result.success) {
                    if(result.data.length > 0) {
                        dispatch({ type: 'SET_STATE', value: { loadingUserList: false, users: [...users, ...result.data], userScroll: ((result.data.length % usersPerPage) === 0) } });
                        return true;
                    } else {
                        dispatch({ type: 'SET_STATE', value: { userScroll: false } });
                    }
                }

                dispatch({ type: 'SET_STATE', value: { loadingUserList: false } });
                return false;
            } catch(e) {
                dispatch({ type: 'SET_STATE', value: { loadingUserList: false } });
                return false;
            }
        },
        // Fetch message list for current user from node server
        getMessages: async (data, token) => {
            try {
                dispatch({ type: 'SET_STATE', value: { loadingMessageList: true, messages: [], scrollToBottom: true, messageScroll: false } });

                const res = await fetch(`${REACT_APP_API_URL}messages`, {
                    method: "POST",
                    mode: "cors",
                    cache: "no-cache",
                    headers: {
                        "Content-Type": "application/json; charset=utf-8",
                        Authorization: `Bearer ${token}`
                    },
                    redirect: "follow",
                    referrer: "no-referrer",
                    body: JSON.stringify(data),
                });
        
                const result = await res.json();
                // console.log(result);
                if(result.success && result.data.length > 0) {
                    dispatch({ type: 'SET_STATE', value: { messages: result.data, loadingMessageList: false, messageScroll: ((result.data.length % messagesPerPage) === 0) } });
                    return true;
                }

                dispatch({ type: 'SET_STATE', value: { loadingMessageList: false } });
                return false;
            } catch(e) {
                dispatch({ type: 'SET_STATE', value: { loadingMessageList: false } });
                return false;
            }
        },
        // Fetch message list for current user from node server
        getScrollMessages: async (data, messages, token) => {
            try {
                dispatch({ type: 'SET_STATE', value: { loadingMessageList: true, scrollToBottom: false } });

                const res = await fetch(`${REACT_APP_API_URL}messages`, {
                    method: "POST",
                    mode: "cors",
                    cache: "no-cache",
                    headers: {
                        "Content-Type": "application/json; charset=utf-8",
                        Authorization: `Bearer ${token}`
                    },
                    redirect: "follow",
                    referrer: "no-referrer",
                    body: JSON.stringify(data),
                });
        
                const result = await res.json();
                // console.log(result);
                if(result.success) {
                    if(result.data.length > 0) {
                        dispatch({ type: 'SET_STATE', value: { loadingMessageList: false, messages: [...result.data, ...messages], messageScroll: ((result.data.length % messagesPerPage) === 0) } });
                        return true;
                    } else {
                        dispatch({ type: 'SET_STATE', value: { messageScroll: false } });
                    }
                }

                dispatch({ type: 'SET_STATE', value: { loadingMessageList: false } });
                return false;
            } catch(e) {
                dispatch({ type: 'SET_STATE', value: { loadingMessageList: false } });
                return false;
            }
        },
        // Set classes currently selected user
        updateUsers: (users, userId) => {
            const newUsers = users.map(n => { 
                n.classes = n.userId === userId ? "person active" : "person";
                return n;
            })
            dispatch({ type: 'SET_STATE', value: { users: newUsers } })
        },
        // Set classes currently selected user
        updateUsersList: (users, newUsers, userId) => {
            let userList = [ ...users ];
            if(newUsers.length < usersPerPage) {
                for(let i = 0; i < newUsers.length; i++) {
                    const user = newUsers[i];
                    const idx = userList.findIndex(n => (n.userId === user.userId));
                    if(idx > -1) {
                        userList[idx] = { ...user };
                    } else {
                        userList = [ ...[user], ...userList ];
                    }
                }
            } else {
                userList = [ ...newUsers ];
            }
            userList = userList.map(n => { 
                n.classes = n.userId === userId ? "person active" : "person";
                return n;
            });
            userList = userList.sort(compare);
            dispatch({ type: 'SET_STATE', value: { users: userList } });
        },
        // Save currently selected user
        setSelectedUser: user => dispatch({ type: 'SET_STATE', value: { selectedUser: user } }),
        setSocketIds: ids => dispatch({ type: 'SET_STATE', value: { socketIds: ids } }),
        // Save messsage sent by current user to MongoDB
        sendMessage: (messages, newMessage, token) => {
            const msgs = [ ...messages, newMessage ];
            dispatch({ type: 'SET_STATE', value: { messages: msgs } });

            fetch(`${REACT_APP_API_URL}savemessage`, {
                method: "POST",
                mode: "cors",
                cache: "no-cache",
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    Authorization: `Bearer ${token}`
                },
                redirect: "follow",
                referrer: "no-referrer",
                body: JSON.stringify(newMessage),
            });
        },
        clearMessageCount: (data, token) => {
            fetch(`${REACT_APP_API_URL}clearmessagecount`, {
                method: "POST",
                mode: "cors",
                cache: "no-cache",
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    Authorization: `Bearer ${token}`
                },
                redirect: "follow",
                referrer: "no-referrer",
                body: JSON.stringify(data),
            });
        },
        updateUserState: (data, token) => {
            fetch(`${REACT_APP_API_URL}updatestate`, {
                method: "POST",
                mode: "cors",
                cache: "no-cache",
                headers: {
                    "Content-Type": "application/json; charset=utf-8",
                    Authorization: `Bearer ${token}`
                },
                redirect: "follow",
                referrer: "no-referrer",
                body: JSON.stringify(data),
            });
        }
    }
}
