import React, { Component } from 'react';
import { connect } from 'react-redux';
import { mapState, mapDispatch } from './actions';
import { format, differenceInDays } from 'date-fns';

class Right extends Component {
    state = { width: 0, height: 0 };
    
    constructor(props) {
        super(props);
        this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
        this.chatRef = React.createRef();
    }

    handleSubmit = (e) => {
        e.preventDefault();
        const data = new FormData(e.target);
        const content = data.get('content').trim();
        const testContent = content.replace(/&nbsp;/g, '').replace(/<p>/g, '').replace(/<\/p>/g, '').trim();
        if(testContent) {
            this.props.sendMessage(
                this.props.messages,
                {
                    datetime: new Date().toISOString(),
                    message: data.get('content'),
                    receiverId: this.props.selectedUser.userId,
                    senderId: this.props.currentUser.userId,
                    senderName: this.props.currentUser.name,
                    senderImage: this.props.currentUser.image,
                    _id: Date.now()
                },
                this.props.token
            )
            window['$R']('#content', 'source.setCode', '');
            this.scrollToBottom();
        }
    }

    componentDidMount() {
        this.updateWindowDimensions();
        window.addEventListener('resize', this.updateWindowDimensions);
        window['$R']('#content', {
            placeholder: 'Type here...', 
            source: false, 
            style:false, 
            linkTarget: '_blank',  
            height: '100%',
            callbacks: {
                drop: function(e) {
                    return false;
                },
                inserted: (nodes) => {
                    for(let x = 0; x < nodes.length; x++) {
                        const node = nodes[x];
                        if(node.nodeName === "FIGURE") {
                            node.parentNode.removeChild(node);
                        }
                    }
                }
            }
        });
    }

    scrollToBottom() {
        // const scrollHeight = this.chatRef.current.scrollHeight;
        // const height = this.chatRef.current.clientHeight;
        // const maxScrollTop = scrollHeight - height;
        // this.chatRef.current.scrollTop = maxScrollTop > 0 ? maxScrollTop : 0;
        setTimeout(() => { 
            const messageLength = this.props.messages.length;
            if(messageLength > 0) {
                document.getElementById(this.props.messages[messageLength - 1]._id).scrollIntoView({block: "end", inline: "nearest"}); 
            }
        }, 500);
    }

    componentDidUpdate() {
        if(this.props.scrollToBottom) {
            this.scrollToBottom();
        }
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWindowDimensions);
    }

    updateWindowDimensions() {
        this.setState({ width: window.innerWidth, height: window.innerHeight });
    }

    loadMoreMessages = async () => {
        try {
            let params = { receiverId: this.props.currentUser.userId, senderId: this.props.selectedUser.userId, skip: this.props.messages.length }
            const lastId = (this.props.messages.length > 0) ? this.props.messages[0]._id : '';

            const result = await this.props.getScrollMessages(params, this.props.messages, this.props.token);
            if(result && lastId) {
                document.getElementById(lastId).scrollIntoView({block: "center", inline: "nearest"});
            }
        } catch(e) {}
    }

    handleScroll = (e) => {
        if(this.props.messageScroll) {
            this.loadMoreMessages();
        }
    }

    messageLoading() {
        let buttonText = 'Load More';
        if(this.props.loadingMessageList) {
            buttonText = 'Loading ...';
        } else if(!this.props.loadingMessageList && this.props.messages.length === 0) {
            buttonText = 'No messages found!';
        } else if(!this.props.messageScroll) {
            return;
            // buttonText = 'No more messages to load!';
        } else {
            buttonText = 'Load More';
        }
        return (
            <div className="message loadmessage">
                <button type="button" disabled={this.props.loadingMessageList || !this.props.messageScroll} className="load-btn" onClick={this.handleScroll}>{buttonText}</button>
            </div>
        );
    }

    render() {
        let divStyle = { height: this.state.height * 0.65 };
        let prevDiff = null;
        const currentUser = this.props.currentUser;
        const redactorStyles = document.querySelector('.redactor-styles');
        if(redactorStyles) { redactorStyles.style.height = `${this.state.height * 0.35 - 110}px`; }

        const online = (this.props.socketIds.indexOf(this.props.selectedUser.userId) > -1);
        const onlineClass = online ? 'green-dot' : 'yellow-dot';
        const onlineTitle = online ? 'Online' : 'Offline';

        let messageList = this.props.messages.map(message => {
            const classes = (message.senderId === currentUser.userId) ? ["message-data pull-right", "bubble me"] : ["message-data", "bubble you"];

            var diff = differenceInDays(new Date(message.datetime), new Date());

            let dateHtml = '';
            if (prevDiff !== diff) {
                if (diff > 0 || diff < 0) {
                    dateHtml = <div className="conversation-start"><span>{format(new Date(message.datetime), 'MMM Do, YYYY')}</span></div>;
                } else if (diff === 0) {
                    dateHtml = <div className="conversation-start"><span>Today</span></div>;
                }
            }
            prevDiff = diff;

            return (
                <div id={message._id} key={message._id}>
                    {dateHtml}
                    <div className="message">
                        <div className={classes[0]}>
                            <span className="message-data-name">{message.senderName}</span>
                            <span className="message-data-time">{format(new Date(message.datetime), 'hh:mm a')}</span>
                        </div>
                        <div className={classes[1]} dangerouslySetInnerHTML={{ __html: message.message }}>
                        </div>
                    </div>
                </div>
            );
        });

        return (
            <div className="right">
                <span className="menu-icon"></span>
                <div className="chat-container">
                    <div className="top">
                        {this.props.selectedUser.name && <span> <span className={onlineClass} title={onlineTitle}></span> <span className="name">{this.props.selectedUser.name}</span></span>}
                    </div>
                    <div className="chat active-chat" ref={this.chatRef} style={divStyle} data-chat="person2">
                        {this.messageLoading()}
                        {messageList}
                    </div>
                </div>
                <form id="messaging_form" onSubmit={this.handleSubmit}>
                    <div className="write" id="toolbar-target">
                        <textarea id="content" name="content"></textarea>
                        <div id="file-target"></div>
                        <button className="post-btn" disabled={this.props.users.length === 0}> <svg xmlns="http://www.w3.org/2000/svg" height="20" viewBox="0 0 1792 1792" width="20"><path fill="#fff" d="M1764 11q33 24 27 64l-256 1536q-5 29-32 45-14 8-31 8-11 0-24-5l-453-185-242 295q-18 23-49 23-13 0-22-4-19-7-30.5-23.5t-11.5-36.5v-349l864-1059-1069 925-395-162q-37-14-40-55-2-40 32-59l1664-960q15-9 32-9 20 0 36 11z" /></svg>Send</button>
                    </div>
                </form>
            </div>
        );
    }
}

export default connect(mapState, mapDispatch)(Right);
