import React, { Component } from 'react';
import './assets/css/message.css';
import './assets/css/redactor.min.css';
import './App.css';
import Left from './Left';
import Right from './Right';
import { connect } from 'react-redux';
import { mapState, mapDispatch } from './actions';
import { socket, addUser } from './socketscript';

class App extends Component {

  constructor(props) {
    super(props);
    socket.on('connect', () => { 
      console.log(`Socket connection: ${socket.connected}`); 
      if(this.props.currentUser.userId) { addUser(this.props.currentUser.userId); }
    })
    this.props.authenticate();
  }

  render() {
    let content = this.props.isAuthenticated 
                    ? <div className="container"><Left /><Right /></div> 
                    : <div className="container"><p className="loader">Loading ...</p></div>
    return (
      <div className="wrapper messaging_wrapper">
        {content}
      </div>
    );
  }
}

export default connect(mapState, mapDispatch)(App);
